
.resume {
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
}

.resume > .resume__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
    max-width: 1024px;
    border: 1px solid var(--bga);
    border-radius: 5px;
    overflow-x: hidden;
}

.resume > .resume__container::before {
    content: "About Me";
    font-size: 2em;
    font-weight: 600;
    color: var(--clro);
    margin: 10px 0;
    text-align: left;
}

.resume > .resume__container > div {
    width: 90%;
    padding: 10px;
    margin: 5px 0;
}


.main_div > .main__container {
    width: min(900px, 90%);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.main_div > .main__container > div {
    width: 45%;
    min-width: 300px;
    padding: 10px;
}

.main_div > .main__container > .main__container__left {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.main_div > .main__container > .main__container__left > div {
    margin: 5px 0;
}

.main_div > .main__container > .main__container__left > .intro > p {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 15px;
    text-align: justify;
}

.main_div > .main__container > .main__container__left > .intro > p::first-letter{
    padding-left: 20px; 
}

.main_div > .main__container > .main__container__left > .mail > a {
    padding: 5px 10px;
    color: var(--clrs);
    font-size: 15px;
    transition: 0.5s;
}

.main_div > .main__container > .main__container__left > .mail > a:hover {
    color: var(--clro);
    transition: 0.5s;
}

.main_div > .main__container > .main__container__right > img {
    width: 100%;
    border-radius: 50%;
    border: 3px solid var(--clrs);
}

.main_div > .main__container > .main__container__left > .resume_pdf {
    background-color: var(--bga);
    border-radius: 5px;
    padding: 5px 10px;
    border: 1px solid var(--clrl);
    transition: 0.5s;
    margin: 10px 0;
    cursor: pointer;
    box-shadow: 2px 2px 5px #0000;
}

.main_div > .main__container > .main__container__left > .resume_pdf:hover {
    box-shadow: 2px 2px 5px #000;
    transition: 0.5s;
}

.main_div > .main__container > .main__container__left > .resume_pdf > a {
    display: flex;
    align-items: stretch;
    color: var(--clrs);
    text-decoration: none;
}

.main_div > .main__container > .main__container__left > .resume_pdf > a > svg {
    padding-left: 5px;
}


.resume > .resume__container > .tech_stack {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

}

.resume > .resume__container > .tech_stack > img{
    opacity: 0.7;
    transition: 0.5s;
}

.resume > .resume__container > .tech_stack > img:hover {
    opacity: 1;
    transition: 0.5s;
}

.resume > .resume__container > .main_div, .experience, .education, .courses, .coding, .contributions {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    color: var(--clrl);
}

.resume > .resume__container > .experience > span {
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    margin: 20px 0;
    letter-spacing: 1px;
}

.experience_card {
    max-width: 800px;
    width: 100%;
    border: 1px solid var(--bga);
    border-radius: 5px;
    margin: 5px 0;
    background-color: rgba(255, 255, 255, 0.074);
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

.experience_card:hover {
    box-shadow: 2px 2px 5px 10px rgba(0, 0, 0, 0.5);
}


.experience_card > .experience_card__header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid var(--clrl);
    flex-wrap: wrap;

}

.experience_card > .experience_card__header > .experience_card__header__left {
    min-width: 300px;
}

.experience_card > .experience_card__header > .experience_card__header__right {
    min-width: 100px;
}

.experience_card > .experience_card__header > .experience_card__header__left > p:first-of-type {
    padding: 0 5px;
    margin: 0;
    font-size: 15px;
    display: flex;
    align-items: center;
}

.experience_card > .experience_card__header > .experience_card__header__left > p:first-of-type > svg {
    margin-right: 5px;
}

.experience_card > .experience_card__header > .experience_card__header__left > p:last-of-type {
    padding: 2px 25px;
    margin: 0;
    font-size: 12px;
    font-style: italic;
}

.experience_card > .experience_card__body {
    text-align: justify;
    padding: 10px;
    font-size: 14px;
}


.education_card {
    max-width: 800px;
    width: 100%;
    border-radius: 5px;
    margin: 5px 0;
    background-color: rgba(255, 255, 255, 0.074);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

.education_card:hover {
    box-shadow: 2px 2px 5px 10px rgba(0, 0, 0, 0.5);
}

.education_card > .education_card__left {
    min-width: 300px;
}

.education_card > .education_card__right {
    min-width: 100px;
}

.education_card > .education_card__left  > p {
    padding: 0 5px;
    margin: 0;
    font-size: 15px;
    display: flex;
    align-items: center;
}

.education_card > .education_card__left  > p > svg {
    margin-right: 5px;
}

.education_card > .education_card__left  > span {
    padding: 2px 25px;
    margin: 0;
    font-size: 12px;
    font-style: italic;
}

.resume > .resume__container > .education > span {
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    margin: 20px 0;
    letter-spacing: 1px;
}

.courses > span {
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    margin: 20px 0;
    letter-spacing: 1px;
}

.courses > .courses__list {
    max-width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

.course_card {
    width: 100%;
    background-color: rgba(255, 255, 255, 0.074);
    border-bottom: 1px solid var(--bga);
    padding: 10px;
}

.course_card:hover {
    box-shadow: 2px 2px 5px 10px rgba(0, 0, 0, 0.5);
}

.course_card:first-child {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.course_card:last-child {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.course_card > .course_name {
    font-size: 14px;
    color: var(--clro);
    font-style: italic;
    margin: 0;
}

.course_card > a {
    color: var(--clrs);
    text-decoration: underline var(--clro);
    font-size: 15px;
}

.course_card > .course_topics {
    font-size: 10px;
    color: var(--clrl);
}

.course__stats {
    font-size: 12px;
    color: var(--clro);
    font-style: italic;
    padding: 2px 10px;
}

.course__stats > p > a {
    display: inline-flex;
    align-items: center;
    color: var(--clro);
    transition: 0.5s;
    width: fit-content;
    margin-left: 5px;
}

.course__stats > p > a:hover {
    color: var(--clrs);
    transition: 0.5s;
}


.coding > .coding__profiles {
    width: min(800px, 90%);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.coding > span {
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    margin: 20px 0;
    letter-spacing: 1px;
}

.coding_card {
    width: fit-content;
    margin: 5px;
    padding: 10px 20px;
}

.coding_card > a {
    padding: 10px 20px;
    text-decoration: none;
    color: var(--clrs);
    border: 1px solid var(--bga);
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.074);
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
    transition: 0.5s;
}

.coding_card > a:hover {
    color: var(--clro);
    transition: 0.5s;
}

.coding__stats {
    font-size: 12px;
    color: var(--clro);
    font-style: italic;
    padding: 2px 10px;
}

.coding__stats > p > a {
    display: inline-flex;
    align-items: center;
    color: var(--clro);
    transition: 0.5s;
    width: fit-content;
    margin-left: 5px;
}

.coding__stats > p > a:hover {
    color: var(--clrs);
    transition: 0.5s;
}

.contributions > span {
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    margin: 20px 0;
    letter-spacing: 1px;
}

.contributions > p {
    width: min(800px, 90%);
    padding: 5px 10px;
    margin: 0 auto;
}

@media screen and (max-width: 400px) {
    .education_card, .experience_card__header {
        justify-content: center !important;
    }
}