
.loader {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: rgb(0, 0, 0);
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader__container {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border:  30px solid var(--clro);
    border-style: dotted;
    animation: loader 2s linear infinite;
}

@keyframes loader {
    0% {
        transform: rotate(0)
    }
    50% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}