.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  color: var(--clrl);
}

.contact-container-top {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contact-container-top > h3 {
  color: var(--clro);
  font-size: 1rem;
  margin: 0 !important;
  letter-spacing: 1px;
}

.contact-container-top > h2 {
  font-size: 3rem;
  margin: 10px !important;
  letter-spacing: 2px;
  font-weight: 500;
}

.contact-container-top > p {
  width: 90%;
  max-width: 500px;
  text-align: center;
  line-height: 25px;

}

.contact-container-bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contact-container-bottom > .socials {
  display: flex;
  /* flex-direction: column; */
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.contact-container-bottom > .socials > .socials-item {
  width: 25vw;
  min-width: 300px;
  height: 60px;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.115);
  font-size: 25px;
  margin: 15px;
  border-radius: 2px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.contact-container-bottom > .socials > .socials-item > svg {
  float: left;
  font-size: 40px;
  color: var(--clrl);
  padding-left: 20px;
}

.contact-container-bottom > .socials > .socials-item > a {
  padding-left: 20px;
  color: var(--clro);
  text-decoration: none;
  width: fit-content;
}

.contact-container-bottom > .socials > .socials-item > a > span {
  position: relative;
  display: inline-block;
  padding: 0 0 0 0;
  transition: padding 0.4s;
}

.contact-container-bottom > .socials > .socials-item > a >span::after {
  content: '';
  display: block;
  width: 0;
  height: 2.5px;
  background: var(--clro);
  transition: width 0.4s;
}

.contact-container-bottom > .socials > .socials-item > a:hover > span::after {
  width: 100%;
  transition: width 0.4s;
}


@media screen and (max-width: 480px) {
  .contact-container-top > h2 {
    letter-spacing: normal;
    text-align: center;
  }
}
  