
/* PROJECT CSS */

.project__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.project_div {
    border-radius : 5px;
    padding : 10px;
    margin : 10px;
    width: min(1024px, 90%);
    min-height: 90vh;
    border: 1px solid var(--bga);
    color: var(--clrl);
}

.project_div > div {
    border-radius: 2px;
    margin: 5px 0;
}

.project_div > .project_head {
    position: relative;
}

.project_div > .project_head > .project_title > h1 {
    padding: 10px;
    color: var(--clrl);
    width: fit-content;
    margin-bottom: 0 !important;
    padding-bottom: 2px !important;

}

.project_div > .project_head > .project_tech_stack > h3 {
    font-size: 10px;
    color: var(--clro);
    font-weight: 100 !important;
    margin-top: 0 !important;
    padding: 0 10px;
}

.project_div > .project_head > .project_links {
    position: absolute;
    top: -30px;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2px 5px;
    height: 40px;
    background-color: #00000077;
    border-radius: 20px;
}

.project_div > .project_head > .project_links > a {
    margin: 0 5px;
    color: var(--clrl);
    font-size: 20px;
}

.project_div > .project_head > .project_links > a:hover {
    color: var(--clro);
    transition: 0.5s;
}

.css-i4bv87-MuiSvgIcon-root {
    font-size: 20px !important;
}

.project_div > .project_media {
    border: 1px solid var(--bga);
}

.project_div > .project_media > .project_media_image {
    width: 100%-10px;
    padding: 10px;
}

.project_div > .project_media > .project_media_image > img {
    width: 100%;
}

.project_div > .project_share {
    height: 0 !important;

}

.project_div > .project_body img {
    width: min(600px, 90%);
    padding: 2px;
    border: 1px solid var(--bga);
}

.project_div > .project_body pre {
    background-color: var(--bga);
    padding: 10px;
    overflow-x: scroll;
    border-radius: 5px;
}

.project_div > .project_body pre > code {
    background-color: #0000;
}

.project_div > .project_body code {
    color: var(--clro);
    background-color: var(--bga);
    padding: 2px;
    border-radius: 5px;
}

.project_div > .project_body a {
    color: var(--clrs);
    text-decoration: wavy underline var(--clro);
}

.project_div > .project_body a:hover {
    color: var(--clro);
    transition: 0.5s;
}

.project_div > .project_body blockquote {
    background-color: var(--bga);
    padding: 10px;
    border-left: 2px solid var(--clro);
    margin-left: 10px !important;
    padding-left: 20px !important;
}

/* SHARE CSS */ 

.share__container {

    position: fixed;
    top: 45vh;
    right: 0;
    padding: 5px;
}

.share__container > .share_div {
    text-align: center;
}

.share__container > .share_div > .share_head {
    font-size: 10px;
    border-radius: 50%;
    background-color: var(--bga);
    width: 50px;
    height: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    cursor: pointer;
    opacity: 0.7;
}

.share__container > .share_div > .share_head:hover {
    opacity: 1;
    color: var(--clro);
    transition: 0.5s;
}

.share__container > .share_div > .share_head > p {
    margin-top: 0 !important;
}

.share__container > .share_div > .share_body {
    transform: scale(0) translate(100vw,-100vh);
    transition: 0.5s;
    margin-top: 10px;
}

.share__container > .share_div > .share_body.shareOn {
    transform: scale(1) translate(0,0);
    transition: 0.5s;
}

.share__container > .share_div > .share_body > .share_body_icons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.share__container > .share_div > .share_body > .share_body_icons > button {
    padding: 5px !important;
}

.share__container > .share_div > .share_body > .share_body_icons > button > svg {
    transition: 0.5s;

}

.share__container > .share_div > .share_body > .share_body_icons > button:hover > svg {
    color: var(--clro);
    transition: 0.5s;
}

/* PROJECT CARD CSS */

.project_card {
    border-radius : 5px;
    padding : 5px;
    margin : 5px;
    width: min(400px, 90%);
    height: 240px;
    border: 1px solid var(--bga);
    background-color: var(--bga);
    color: var(--clrl);
    position: relative;
    transition: 0.5s;
    cursor: pointer;
    box-shadow: 2px 2px 10px #000;
}

.project_card:hover {
    transition: 0.5s;
    background-color: #0000;
}

.project_card > .project_card_head {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    height: fit-content;
    width: fit-content;
    background-color: var(--bgs);
    box-shadow: 2px 2px 10px #000;
    z-index: 1;
    /* opacity: 0.9; */
    border-radius: 0 0 25px 25px;
}

.project_card > .project_card_head > .project_card_title > p {
    font: 15px;
    font-weight: 600;
    color: var(--clrl);
    width: fit-content;
    margin: 0 !important;
    padding-bottom: 2px !important;
}

.project_card > .project_card_head > .project_card_tech_stack > p {
    font-size: 10px;
    color: var(--clro);
    font-weight: 100 !important;
    margin: 0 !important;
    padding: 0 10px;
}

.project_card > .project_card_body {
    width: 100%;
    height: 200px;
    overflow: hidden;
    background-color: #000; /*var(--clrs);*/
}

.project_card > .project_card_body > img {
    width: 100%;
    height: 100%;
    transition: 0.5s;
    background-color: var(--bga);

}

.project_card > .project_card_body > img:hover {
    transform: scale(1.1);
    transition: 0.5s;
}

.project_card > .project_card_footer {
    padding: 5px;
    height: 30px;
    text-overflow: ellipsis;
}

.project_card > .project_card_footer > p {
    margin: 0;
    font-size: 12px;
    height: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
}


/* PROJECT BOX CSS */

.projects_ele {
    margin: 10px;
    border: 1px solid var(--bga);
    color: var(--clrl);

}

.projects_ele > .projects_ele_title {
    margin-left: 25px;
}

.projects_ele > .projects_ele_title > p {
    padding-top: 10px;
    font-size: 20px;
    font-weight: 700;
    margin: 0;
}

.projects_ele > .projects_ele_title > span {
    font-size: 12px;
    color: var(--clro);
}


.projects_ele > .projects_ele_body {
    padding: 5px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;    
    justify-content: center;
}

.projects_ele > .projects_ele_body > a {
    width: min(400px, 90%);
    text-decoration: none;
}

.projects_ele > .projects_ele_footer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.projects_ele > .projects_ele_footer > .show_more {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    padding: 5px;
}

.projects_ele > .projects_ele_footer > .show_more:hover {
    color: var(--clro);
}

/* PROJECTS CSS */

.projects_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
