

header.main-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 56px;
  background-color: var(--bgl);
  background-image: url(/src/Components/img/bg.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  z-index: 90;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* position: relative; */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}