

.main-navigation__menu-btn {
    display: none;
    margin: 1rem;
    cursor: pointer;
}

.main-navigation__menu-btn > span {
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background: var(--clro);
    border-radius: 3px;
    z-index: 1;
    transform-origin: 5px 0px;
    transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0), background 0.5s cubic-bezier(0.77,0.2,0.05,1.0), opacity 0.55s ease;
}

.main-navigation__menu-btn.open > span:first-child {
    transform: rotate(45deg);
}

.main-navigation__menu-btn.open > span:nth-last-child(2) {
    opacity: 0;
}

.main-navigation__menu-btn.open > span:last-child {
    transform: rotate(-45deg);
}

.main-navigation__title > a {
    text-decoration: none;
    color: var(--clro);
    font-size: 2.5rem;
    font-weight: 500;
    padding: 0 2rem;
}

.main-navigation__header-nav {
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.main-navigation__header-nav li {
    height: 56px !important;
    display: flex;
    align-items: center;
}
.main-navigation__header-nav li a {
    padding: 1rem !important;
    /* height: 56px !important; */
}

@media screen and (max-width: 768px) {

    .main-navigation__menu-btn {
        display: block;
    }

    .main-navigation__header-nav {
        display: none;
    }
    
}