
.nav-links {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.nav-links li {
  list-style: none;
  margin: 0 0.5rem;
}

.nav-links > li > a {
  color: var(--clrl);
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 500;
  padding: 0.5rem 1rem;
  /* border-radius: 5px; */
  transition: all 0.3s ease;
  border-bottom: 2px solid transparent;
}

.nav-links > li > a:hover{
  color: var(--clrs);
}

.nav-links > li > a.active {
  color: var(--clrs);
  border-bottom: 2px solid var(--clro);
}
