

.short-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.short-cards > .short-card {
    width: 90%;
    max-width: 400px;
    min-width: 300px;
    margin: 10px 0;
    background-color: rgb(78, 78, 78);
    border-radius: 10px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    box-shadow: 3px 3px 0px rgba(255, 255, 255, 0.73), 5px 5px 5px rgb(0, 0, 0);
}

/* .short-cards > .short-card > .short-card__main {
} */

.short-cards > .short-card > .short-card__main > .short-card__title {
    font-size: 1.5rem;
    font-weight: 600;
    margin: 5px 0;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}

.short-cards > .short-card > .short-card__main > .short-card__intro {
    font-size: 0.8rem;
    font-weight: 400;
    margin-bottom: 5px;
}

.short-cards > .short-card > .short-card__content {
    background-color: rgba(255, 255, 255, 0.254);
    border-radius: 5px;
}

.short-card__content__div {
    border-bottom: 1px solid rgba(0, 0, 0, 0.437);
}

.short-card__content__div > .short-card__content__code {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.437);
    background-color: #333;
    color: rgb(255, 111, 44);
    font-family: 'Courier New', Courier, monospace;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
}

.short-card__content__div > .short-card__content__code > .short-card__content__code__text {
    width: calc(100% - 30px);
    padding: 3px 5px;
    position: relative;
}

.short-card__content__div > .short-card__content__code > .short-card__content__code__text > span {
    overflow-y: scroll;
}

.short-card__content__div > .short-card__content__code > .short-card__content__code__text > .short-card__content__code__copy {
    opacity: 0.5;
    position: absolute;
    top: 0;
    right: 0;
    background-color: rgba(34, 34, 34, 0.59);
}

.short-card__content__div > .short-card__content__code > .short-card__content__code__text > .short-card__content__code__copy:hover {
    opacity: 1;
}

.short-card__content__div > .short-card__content__code > .short-card__content__desc__toggle {
    width : 30px;
    background-color: rgb(59, 59, 59);
    color: #fff;
    text-align: center;
}

.short-card__content__div > .short-card__content__desc {
    transform: scaleY(0);
    height: 0;
    transition: height 0.50ms, transform 0.50ms;
    font-size: 10px;
    font-weight: bold;
    font-style: italic;
    color: #222;
    font-family: cursive ,'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.short-card__content__div > .short-card__content__desc.short-card__content__desc--expand {
    transform: scaleY(1);
    height: auto;
    transition: height 0.50ms, transform 0.50ms;   
    padding: 5px;
    background-color: rgba(255, 255, 255, 0.304);
}