
.home {
    /* height: 100vh; */
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background: var(--bgs); */
    flex-wrap: wrap;
}

.det-name {
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 1px;
}


.det-name::after {
    content: " ";
    display: block;
    width: 60px;
    height: 5px;
    background: var(--clro);
    margin: 10px 0;
}

.det-social-item > a {
    color: var(--clrs);
    text-decoration: none;
    font-weight: bold;
    letter-spacing: 1px;
}

.det-social-item > a:hover {
    color: var(--clro);
}

.det-contact > p {
    color: var(--clro);
    font-weight: bold;
    border: 3px solid var(--clro);
    width: fit-content;
    padding: 15px;
    letter-spacing: 1px;
    border-radius: 2px;
    cursor: pointer;
}

.det-contact > p > a {
    color: var(--clro);
    text-decoration: none;
}

.det-contact > p:hover {
    background: var(--clro);
    transition: 0.5s;
}

.det-contact > p:hover > a {
    color: var(--bgs);
    transition: 0.5s;
}

.odot {
    color: var(--clro);
    /* padding: 3px; */
    /* font-size: 40px; */
}

.mnk-img {
    max-width: 500px;
    min-width: 300px;
    width: 50vw; 
    /* border: 2px solid var(--clro); */
    /* box-shadow: 10px 10px 100px var(--clro); */
}

.intro {
    max-width: 300px;
}

.intro-heading {
    text-transform: uppercase;
    font-weight: 700;
    color: var(--clro);
    letter-spacing: 2px;
}

.intro-role {
    font-size: 30px;
    font-weight: 600;
}

.learn-more > a {
    color: var(--clro);
    text-decoration: none;
    font-weight: bold;
}


@media screen and (max-width: 480px) {
    .logo-box {
        margin-left: 0;
    }
}

@media screen and (max-width: 320px) {
    .det-name {
        font-size: 20px;
    }
    .det-social-item > a {
        font-size: 15px;
    }
    .det-contact > p {
        font-size: 15px;
    }
    .mnk-img {
        margin-left: 0px;
    }
    .det {
        margin-right: -70px;
    }
    .home {
        align-items: flex-start;
    }
}