
.side-drawer {
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    z-index: 200;
    height: 100vh;
    background-color: var(--bgl);
    box-shadow: 1px 0 6px rgba(0, 0, 0, 0.26);
    display: none;
}

.side-drawer > nav > ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 5px;
}

.side-drawer > nav > ul > li {
    list-style: none;
    margin: 1rem 0.5rem;
}

@media screen and (max-width: 768px) {
    .side-drawer {
      display: block;
    }
  }